.developer__link {
  text-decoration: none;
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    padding: 0;
    display: flex;
    gap: 23px;
  }
}

.developer__text {
  color: $text-grey400;
  font-family: $font-lato;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
}
