@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.page__body {
  margin: 0;
}

.visibility-none {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}

.button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 8px 44px;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  border: none;
  background-color: #68b738;
  color: #ffffff;
  margin: 0;
  box-sizing: border-box;
  text-align: center;
}

.button--primary {
  font-size: 16px;
  line-height: 20px;
  padding: 10px 5px;
  width: 100%;
}
@media (min-width: 768px) {
  .button--primary {
    font-size: 20px;
    font-weight: 400;
  }
}

.button--secondary {
  color: #444444;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  background-color: #f2f2f2;
  padding: 10px 0;
}

